import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import {  AlertController} from 'ionic-angular';
/**
 * Generated class for the MessagePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
/**
 * Generated class for the RegisterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
 export class RegisterPage {
   /**
    * @name form
    * @type {FormGroup} 
    * @public
    * @description     Define FormGroup property for managing form validation / data retrieval
    */
   public form                   : FormGroup;




   /**
    * @name requestName
    * @type {Any} 
    * @public
    * @description     Model for managing requestName field
    */
   public requestName         : any;




   /**
    * @name requestDescription
    * @type {Any} 
    * @public
    * @description     Model for managing requestDescription field
    */
   public requestDescription  : any;


 /**
    * @name requestEmail
    * @type {Any} 
    * @public
    * @email     Model for managing requestEmail field
    */
   public requestEmail  : any;

/**
    * @name requestMobile
    * @type {Any} 
    * @public
    * @mobile     Model for managing requestMobile field
    */
   public requestMobile  : any;



   /**
    * @name isEdited
    * @type {Boolean} 
    * @public
    * @description     Flag to be used for checking whether we are adding/editing an entry
    */
   public isEdited               : boolean = false;




   /**
    * @name hideForm
    * @type {Boolean} 
    * @public
    * @description     Flag to hide the form upon successful completion of remote operation
    */
   public hideForm               : boolean = false;




   /**
    * @name pageTitle
    * @type {String} 
    * @public
    * @description     Property to help set the page title
    */
   public pageTitle              : string;




   /**
    * @name recordID
    * @type {String} 
    * @public
    * @description     Property to store the recordID for when an existing entry is being edited
    */
   public recordID               : any      = null;




   /**
    * @name baseURI
    * @type {String} 
    * @public
    * @description     Remote URI for retrieving data from and sending data to
    */
   private baseURI               : string  = "https://qaprofil.com.qa/ionic/";



    
   // Initialise module classes
 constructor(public navCtrl    : NavController,
               public http       : HttpClient,
               public NP         : NavParams,
               public fb         : FormBuilder,
                public alertCtrl: AlertController,
                 
               public toastCtrl  : ToastController)   {

      // Create form builder validation rules
      this.form = fb.group({
         "name"                  : ["", Validators.required],
         "email"                  : ["", Validators.required],
         "mobile"                  : ["", Validators.required],
         "description"           : ["", Validators.required]
      });
   }



   
   /**
    * Triggered when template view is about to be entered
    * Determine whether we adding or editing a record
    * based on any supplied navigation parameters
    *
    * @public
    * @method ionViewWillEnter 
    * @return {None}
    */
   ionViewWillEnter() : void
   {
      this.resetFields();

      if(this.NP.get("record"))
      {
         this.isEdited      = true;
         this.selectEntry(this.NP.get("record"));
         this.pageTitle     = 'Amend entry';
      }
      else
      {
         this.isEdited      = false;
         this.pageTitle     = 'Create entry';
      }
   }



   
   /**
    * Assign the navigation retrieved data to properties
    * used as models on the page's HTML form
    *
    * @public
    * @method selectEntry
    * @param item 		{any} 			Navigation data
    * @return {None}
    */
   selectEntry(item : any) : void
   {
      this.requestName        = item.name;
      this.requestEmail        = item.email;
      this.requestMobile       = item.mobile;
      this.requestDescription = item.description;
      this.recordID              = item.id;
   }



   
   /**
    * Save a new record that has been added to the page's HTML form
    * Use angular's http post method to submit the record data 
    *
    * @public
    * @method createEntry
    * @param name 			{String} 			Name value from form field
    * @param description 	{String} 			Description value from form field
    * @return {None}
    */
   sigin(name : string,email : string,mobile : string, description : string) : void
   {
    
      let headers 	: any		= new HttpHeaders({ 'Content-Type': 'application/json' }),
          options 	: any		= { "key" : "create", "name" : name, "email" : email, "mobile" : mobile, "description" : description },
          url       : any      	= this.baseURI + "register_user.php";

 



      this.http.post(url, JSON.stringify(options), headers)
      
      .subscribe((i : any) => 
      {
      



         console.dir(i);
         
         if(i=="Registration successfull"){
       
          
    let alert = this.alertCtrl.create({
      title:'CONGRATS'   ,
      subTitle:("Your Login success"),
      buttons: ['OK']
      });
   
      alert.present();
      this.navCtrl.push('LoginPage',{name : email});
      
       

  }
         
  else
  {
  let alert = this.alertCtrl.create({
      title:"Error",
      subTitle:(i),
      buttons: ['OK']
      });
     
      alert.present();
      
  }
         
         
         
         
      },
      (error : any) =>
      {
         console.dir(error);
      });
   
   }


 

 
 



   
   /**
    * Clear values in the page's HTML form fields  
    *
    * @public
    * @method resetFields
    * @return {None}
    */
   resetFields() : void
   {
      this.requestName           = "";
      this.requestEmail    = ""; 
      this.requestMobile    = ""; 
      this.requestDescription    = ""; 
   }




   /**
    * Manage notifying the user of the outcome of remote operations  
    *
    * @public
    * @method sendNotification
    * @param message 	{String} 			Message to be displayed in the notification
    * @return {None}
    */
   sendNotification(message : string)  : void
   {
      let notification = this.toastCtrl.create({
          message       : message,
          duration      : 3000
      });
      notification.present();
   }


}




